import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import {GitsearchComponent} from './gitsearch/gitsearch.component';
import {PaginateComponent} from './paginate/paginate.component';
import {TxtComponent} from './home/txt/txt.component';
import {NumberComponent} from './home/number/number.component';
import {ImgComponent} from './home/img/img.component';




const routes: Routes = [
    { path: '', redirectTo: '/home', pathMatch: 'full' },
    { path: 'home', component: HomeComponent, children: [
    { path: '', redirectTo: 'img', pathMatch: 'full' },
    { path: 'txt', component: TxtComponent },
    { path: 'number', component: NumberComponent },
    { path: 'img', component: ImgComponent },
] },
    { path: 'about', component: AboutComponent },
    { path: 'gitsearch', component: GitsearchComponent },
    { path: 'paginate', component: PaginateComponent },
];
@NgModule({
    imports: [
        CommonModule,
        RouterModule.forRoot(routes)
    ],
    declarations: [],
    exports: [ RouterModule ]
})
export class AppRoutingModule { }