import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
    providedIn: 'root'
})
@Injectable()
export class UnsplashService {
    readonly apikey = '1dcac9804322f89a7f23a5f6ec79d9a295a721a562e21d8f7cedb815a9f0aa89';
    readonly URL = `https://api.unsplash.com/photos/?page=1&client_id=${this.apikey}`;


    constructor(
        public http: HttpClient
    ) {
    }

    getImgUnsplash(): Observable<any> {
        return this.http.get(this.URL);
    }

    getImages(num): Observable<any> {
        return this.http.get(
            `https://api.unsplash.com/photos/?page=${num}&client_id=${this.apikey}`
        );
    }

}
